/* styles.css */
.custom-border td {
    border: 1px solid #d1d5db; /* Tailwind gray-300 */
  }
  
  .custom-border th {
    border: 1px solid #d1d5db; /* Tailwind gray-300 */
  }
  .animate-modal {
    animation: modal-animation 3s forwards;
  }
  
  @keyframes modal-animation {
    0% {
      opacity: 0;
      transform: scale(0.5);
    }
    50% {
      opacity: 1;
      transform: scale(1);
    }
    100% {
      opacity: 1;
      transform: scale(1);
    }
  }

  .upload-success-gif {
    width: 100%;
    height: 100%;
    object-fit: contain;
    margin: 20px auto;
  }

  .play-gif {
    animation: play-gif 0.5s forwards;
  }
  
  @keyframes play-gif {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }